<template>
	<div class="container m-t-20">
		<div class="title is-4 has-text-centered">Monthly Cost Details</div>
		<table class="table is-fullwidth is-narrow">
			<thead>
				<tr>
					<th class="is-1"></th>
					<th class="is-1">No.</th>
					<th>Monthly: Item</th>
					<th class="is-4">Qty</th>
					<th class="is-3">Unit</th>
					<th class="is-5">Price</th>
					<th class="is-5">Amount</th>
					<th>Note</th>
					<th class="is-2">
						<div @click="addRow()" class="button is-small is-success">
							<span class="icon">
								<i class="fa fa-plus"></i>
							</span>
						</div>	
					</th>
				</tr>
			</thead>
			<draggable :options="{handle: '.sort_handle'}" @end="reorderNumbers()" v-model="tableData" element="tbody">
				<tr v-for="(data, i) in tableData">
					<td class="sort_handle">
						<div class="button is-small">
							<span class="icon">
								<i class="fa fa-arrows"></i>
							</span>
						</div>
					</td>
					<td>{{ data.item }}</td>
					<td>
						<input type="text" class="input is-small" v-model="tableData[i].desc">
					</td>
					<td>
						<input @input="computeAmount(i)" type="text" class="input is-small" v-model="tableData[i].qty">
					</td>
					<td>
						<input type="text" class="input is-small" v-model="tableData[i].unit">
					</td>
					<td>
						<input @input="computeAmount(i)" type="text" class="input is-small" v-model="tableData[i].price">
					</td>
					<td>
						<input type="text" class="input is-small" readonly :value="tableData[i].amount">
					</td>
					<td>
						<input type="text" class="input is-small" v-model="tableData[i].comment">
					</td>
					<td>
						<div @click="removeRow(i)" class="button is-danger is-small">
							<span class="icon">
								<i class="fa fa-close"></i>
							</span>
						</div>
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						{{ tableData.length + 1 }}
					</td>
					<td>
						Consumption Tax
					</td>
					<td></td>
					<td>
						<input @input="updateConsumptionTax()" type="number" v-model="consumption_tax.unit" class="input is-small">
					</td>
					<td>
						<input type="number" readonly class="input is-small" :value="consumption_tax.price">
					</td>
					<td>
						<input readonly type="number" class="input is-small" :value="consumption_tax.amount">
					</td>
					<td></td>
					<td></td>
				</tr>
			</draggable>
		</table>
	</div>
</template>

<script>
	import draggable from 'vuedraggable';
	export default {
		data() {
			return {
				tableData: [],
				consumption_tax: {
					price: 0,
					unit: 1,
					amount: 0,
				},
			}
		},
		components: {
			draggable
		},
		computed: {
			monthly_cost() {
				return (this.consumption_tax.price + this.consumption_tax.amount);
			}
		},
		mounted() {
			// Get dummy data
			axios.get('http://json.invite-comm.jp/api/json/monthly')
			.then(res => {
				this.tableData = res.data;
				this.updateConsumptionTax();
				this.$emit('monthlyCostUpdated', this.monthly_cost);
			}).catch(e => {
				console.log(e);
			});
		},
		methods: {
			addRow() {
				this.tableData.push(
					{
						"record" : "",
						"item" : (this.tableData.length + 1),
						"desc" : "",
						"qty" : "",
						"unit" : "",
						"price" : "",
						"amount" : "",
						"comment" : ""
					}
				);
			},
			removeRow(index) {
				this.tableData.splice(index, 1);
				this.reorderNumbers();
				this.updateConsumptionTax();
			},
			reorderNumbers() {
				this.tableData.forEach((data, index) => {
					this.tableData[index].item = (index + 1);
				});
			},
			computeAmount(index) {
				this.tableData[index].amount = this.tableData[index].price * this.tableData[index].qty;
				this.updateConsumptionTax();
			},
			updateConsumptionTax() {
				this.consumption_tax.price = 0;
				this.tableData.forEach(td => {
					this.consumption_tax.price += td.amount;
				});
				this.consumption_tax.amount = ((this.consumption_tax.unit/100) * this.consumption_tax.price);
				this.$emit('monthlyCostUpdated', this.monthly_cost);
			}
		}
	}
</script>


