<template>
  <div id="app">
    <headerComponent></headerComponent>
    <div class="container">
      <hr class="m-t-35">
    </div>
    <installationComponent v-on:initialCostUpdated="initialCostUpdated"></installationComponent>
    <div class="container">
      <hr class="m-t-35">
    </div>
    <monthlyComponent v-on:monthlyCostUpdated="monthlyCostUpdated"></monthlyComponent>
    <div class="container">
      <hr class="m-t-35">
    </div>
    <noteComponent></noteComponent>
    <div class="container m-t-40">
      <div class="footer">
        <div class="content has-text-centered">
          &copy; 2018. Aseem Lalfakawma
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import headerComponent from './components/headerComponent.vue';
import installationComponent from './components/installationComponent.vue';
import monthlyComponent from './components/monthlyComponent.vue';
import noteComponent from './components/noteComponent.vue';

export default {
  name: 'App',
  components: {
    headerComponent,
    installationComponent,
    monthlyComponent,
    noteComponent
  },
  methods: {
    monthlyCostUpdated(monthlyCost) {
      this.$bus.$emit('monthlyCostUpdated', monthlyCost);
    },
    initialCostUpdated(initialCost) {
      this.$bus.$emit('initialCostUpdated', initialCost);
    }
  }
}
</script>

<style lang="css">
  @import "../node_modules/font-awesome/css/font-awesome.min.css";
</style>
<style lang="scss">
  @import "~bulma/bulma";
  @import "assets/scss/app";
</style>


