<template>
	<div class="container">
		<div class="title is-4 has-text-centered">Notes</div>
		<table class="table is-fullwidth is-narrow">
			<thead>
				<tr>
					<th class="is-1"></th>
					<th class="is-1">No.</th>
					<th>Note</th>
					<th class="is-1">
						<div @click="addRow()" class="button is-small is-success">
							<span class="icon">
								<i class="fa fa-plus"></i>
							</span>
						</div>	
					</th>
				</tr>
			</thead>
			<draggable :options="{handle: '.sort_handle'}" @end="reorderNumbers()" v-model="tableData" element="tbody">
				<tr v-for="(data, i) in tableData">
					<td class="sort_handle">
						<div class="button is-small">
							<span class="icon">
								<i class="fa fa-arrows"></i>
							</span>
						</div>
					</td>
					<td>{{ data.item }}</td>
					<td>
						<input type="text" class="input is-small" v-model="tableData[i].text">
					</td>
					<td>
						<div @click="removeRow(i)" class="button is-danger is-small">
							<span class="icon">
								<i class="fa fa-close"></i>
							</span>
						</div>
					</td>
				</tr>
			</draggable>
		</table>
	</div>
</template>

<script>
	import draggable from 'vuedraggable';
	export default {
		data() {
			return {
				tableData: [],
			}
		},
		mounted() {
			// Get dummy data
			axios.get('http://json.invite-comm.jp/api/json/notes')
			.then(res => {
				this.tableData = res.data;
			}).catch(e => {
				console.log(e);
			});
		},
		components: {
			draggable
		},
		methods: {
			addRow() {
				this.tableData.push(
					{
						"item" : (this.tableData.length + 1),
						"text" : ""
					}
				);
			},
			removeRow(index) {
				this.tableData.splice(index, 1);
				this.reorderNumbers();
			},
			reorderNumbers() {
				this.tableData.forEach((data, index) => {
					this.tableData[index].item = (index + 1);
				});
			},
			extractJSON() {
				return JSON.stringify(this.tableData);
			}
		}
	}
</script>


