<template>
	<div class="container">

		<nav class="navbar">
			<div class="navbar-brand">
				<div class="navbar-item">
					<img src="../assets/logo.png" alt="LOGO">
				</div>
				<div class="navbar-item title is-4">
					QUOTATION AND ORDER SHEET
				</div>
			</div>
			<div class="navbar-menu">
				<div class="navbar-end">
					<div class="navbar-item has-text-weight-bold">
						Issue date: {{ issue_date }}
					</div>
					<div class="navbar-item has-text-weight-bold">
						Quote No. {{ quote_number }}
					</div>
				</div>
			</div>
		</nav>

		<div class="columns">
			<div class="column"></div>
			<div class="column is-four-fifths">
				<div class="title is-4 has-text-centered m-t-10 m-b-10">{{ company_name }}</div>
				<hr class="m-t-0 m-b-10">
				<div class="field is-horizontal">
				  <div class="field-label is-normal">
				    <label class="label is-size-7">Service Type:</label>
				  </div>
				  <div class="field-body">
				    <div class="field">
				      <p class="control">
				        <input class="input is-small" type="text" v-model="service_type">
				      </p>
				    </div>
				  </div>
				</div>
				<div class="field is-horizontal">
				  <div class="field-label is-normal">
				    <label class="label is-size-7">Delivery Date:</label>
				  </div>
				  <div class="field-body">
				    <div class="field">
				      <p class="control">
				        <input class="input is-small" type="date" v-model="delivery_date">
				      </p>
				    </div>
				  </div>
				</div>
				<div class="field is-horizontal">
				  <div class="field-label is-normal">
				    <label class="label is-size-7">Payment Terms:</label>
				  </div>
				  <div class="field-body">
				    <div class="field">
				      <p class="control">
				        <input class="input is-small" type="text" v-model="payment_terms">
				      </p>
				    </div>
				  </div>
				</div>
				<div class="field is-horizontal">
				  <div class="field-label is-normal">
				    <label class="label is-size-7">Expiration Date:</label>
				  </div>
				  <div class="field-body">
				    <div class="field">
				      <p class="control">
				        <input class="input is-small" type="date" v-model="expiration_date">
				      </p>
				    </div>
				  </div>
				</div>
				<div class="field is-horizontal m-t-50">
				  <div class="field-label is-normal">
				    <label class="label is-size-7">Initial Cost:</label>
				  </div>
				  <div class="field-body">
				    <div class="field">
				      <p class="control">
				        <input class="input is-small" readonly type="text" :value="initial_cost">
				      </p>
				    </div>
				  </div>
				</div>
				<div class="field is-horizontal">
				  <div class="field-label is-normal">
				    <label class="label is-size-7">Monthly Cost:</label>
				  </div>
				  <div class="field-body">
				    <div class="field">
				      <p class="control">
				        <input class="input is-small" readonly type="text" :value="monthly_cost">
				      </p>
				    </div>
				  </div>
				</div>
			</div>
			<div class="column"></div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				issue_date: '2018/09/07',
				quote_number: '5B9203D530321',
				company_name: null,
				service_type: null,
				delivery_date: null,
				payment_terms: null,
				expiration_date: null,
				initial_cost: 0,
				monthly_cost: 0,
			}
		},
		mounted() {
			// Get dummy data to be populated
			axios.get('http://json.invite-comm.jp/api/json/company')
			.then(res => {
				const data = res.data;
				this.company_name = data.company;
				this.service_type = data.type;
				this.delivery_date = data.delivery.replace(/\//g, '-');
				this.payment_terms = data.terms;
				this.expiration_date = '2009-08-22'; // Not provided by api
			}).catch(e => {
				console.log(e);
			});

			// Listen to change events
			this.$bus.$on('monthlyCostUpdated', (monthlyCost) => {
				this.monthly_cost = monthlyCost;
			});
			this.$bus.$on('initialCostUpdated', (initialCost) => {
				this.initial_cost = initialCost;
			});
		}
	}
</script>


